@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --bilal-title-font: Inter;

  /* font sizes */
  --font-size-21xl: 40px;
  --font-size-31xl: 50px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-tomato-100: #de3e33;
  --color-tomato-200: rgba(222, 62, 51, 0.77);
  --color-tomato-300: rgba(222, 62, 51, 0.5);
  --bilal-green-color1: #1fd431;
  --bilal-green-color: rgba(31, 212, 49, 0.5);

  /* border radiuses */
  --br-81xl: 100px;
}
